import { NodeService } from '../services_registry'

export interface MarginNodeData {
    top: number
    right: number
    bottom: number
    left: number
}

export const MarginNodeService: NodeService<'node:margin', MarginNodeData> = {
    type: 'node:margin',
    category: 'dimensions',
    description: `Top/right/bottom/left margins for charts.`,
    hasOutput: true,
    properties: [
        {
            type: 'property:number',
            name: 'top',
            accepts: ['property:number'],
            hasOutput: true,
        },
        {
            type: 'property:number',
            name: 'right',
            accepts: ['property:number'],
            hasOutput: true,
        },
        {
            type: 'property:number',
            name: 'bottom',
            accepts: ['property:number'],
            hasOutput: true,
        },
        {
            type: 'property:number',
            name: 'left',
            accepts: ['property:number'],
            hasOutput: true,
        },
    ],
    factory: (data = {}) => {
        return {
            top: data?.top ?? 0,
            right: data?.right ?? 0,
            bottom: data?.bottom ?? 0,
            left: data?.left ?? 0,
        }
    },
    getValue: ({ properties }, registry) => {
        const value: any = {}
        properties.forEach(property => {
            value[property.name] = registry
                .getPropertyService(property.type)
                .getValue(property, registry)
        })

        return value
    },
}
